import type { CommunityNotification } from "./notificationsMock";

function getActor(n: any) {
  return (
    n?.actor?.name ??
    n?.actorName ??
    n?.user?.name ??
    n?.userName ??
    n?.name ??
    n?.author ??
    n?.from ??
    n?.gymName ??
    "Notificación"
  );
}

function getAction(n: any) {
  return (
    n?.action ??
    n?.verb ??
    n?.typeLabel ??
    n?.message ??
    n?.text ??
    "envió una notificación"
  );
}

function getTarget(n: any) {
  return n?.target ?? n?.object ?? n?.className ?? n?.wodTitle ?? n?.topic ?? "";
}

function safeDate(n: any) {
  const raw = n?.date ?? n?.createdAt ?? n?.at ?? n?.time ?? n?.timestamp;
  const d = raw ? new Date(raw) : new Date();
  return isNaN(d.getTime()) ? new Date() : d;
}

function formatDateES(d: Date) {
  // "03 Ene 2026"
  const dd = String(d.getDate()).padStart(2, "0");
  const months = ["Ene","Feb","Mar","Abr","May","Jun","Jul","Ago","Sep","Oct","Nov","Dic"];
  const mm = months[d.getMonth()] ?? "";
  return `${dd} ${mm} ${d.getFullYear()}`;
}

export default function NotificationItem({
  item,
}: {
  item: CommunityNotification | any;
}) {
  const d = safeDate(item);
  const actor = getActor(item);
  const action = getAction(item);
  const target = getTarget(item);

  const title =
    item?.title ??
    item?.subject ??
    item?.headline ??
    ""; // opcional

  const subtitle =
    item?.description ??
    item?.body ??
    item?.subtitle ??
    ""; // lo que sería “mensaje”

  return (
    <div className="px-5 py-4">
      <div className="flex items-start justify-between gap-4">
        <div className="min-w-0">
          <div className="mb-1 space-x-1 text-sm text-gray-500 dark:text-gray-400">
            <span className="font-medium text-gray-800 dark:text-white/90">
              {actor}
            </span>
            <span>{action}</span>
            {target ? (
              <span className="font-medium text-gray-800 dark:text-white/90">
                {target}
              </span>
            ) : null}
          </div>

          {title ? (
            <div className="text-sm font-medium text-gray-800 dark:text-white/90">
              {title}
            </div>
          ) : null}

          {subtitle ? (
            <div className="mt-1 text-sm text-gray-700 dark:text-gray-200">
              {subtitle}
            </div>
          ) : null}
        </div>

        <div className="shrink-0 text-xs text-gray-500 dark:text-gray-400">
          {formatDateES(d)}
        </div>
      </div>
    </div>
  );
}
